@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Fjalla+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');
$backgroundColor : rgba(200, 200, 200, 0.25);
$backgroundColorHover: rgba(200, 200, 200, 0.33);
@import './nav';
.is-white {
    color: #000;
}
.app-name {
    font-family: 'Fjalla One';
    
}
.is-bold {
    font-weight: 600;
}
.fa-angle-left, .fa-angle-right {
    &:hover {
        cursor: pointer;
    }
}
.year-value {
    width: 100px !important;
    background: #222 !important;
    border: 0 !important;
    color: #000 !important;
    font-weight: 600 !important;
    font-family: 'Poppins';
}

input {
    border:none !important;
    outline: none !important;
    box-shadow: none !important;
    // border: 2px solid #000 !important;
}
.fa-inbox, .fa-bell-o {
    &:hover {
        background-color: rgba(196, 196, 196, 0.3);
        border-radius: 50%;
        cursor: pointer;
    }
}
.todays-date {
    font-weight: 500;
}
.today {
    color: #30861B;
    background-color: rgba(48, 178, 61, 0.2);
    border-bottom: 2px solid #30861B ;
    border-width: 2px;
}
.dot {
    height: 4px !important;
    width: 4px !important;
    background-color:#7F00AC;
    border-radius: 50%;
}
.cursor-p {
    cursor: pointer;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.dropdown{
    position: relative;
    &:hover {
        cursor: pointer;
    }
    ul {
        padding: 0 !important;
        text-align: center !important;
    }
    li {
        width: 100% !important;
        &:hover {
            background-color: #e0dddd;
        }
        span {
            padding: 1rem;
        }
    }

}
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #fff;
    font-weight: 500;
    font-family: 'poppins';
    padding-top: 7px;
    text-align: left !important;
    z-index: 1;
    border: 2px solid #c4c4c4 !important;
    border-radius: 6px;
    &:hover {
        cursor: pointer;
        
    }
  
  }
  .dropdown:hover .dropdown-content {
    display: block;
    animation-name: dropdown;
    animation-duration: 0.5s;
  }
  .dropdown-months {
    //   &:hover {
    //     border-bottom: 2px solid #000;
    //   }
      
  }
  @keyframes dropdown {
    from {
        opacity: 0;
    }
    to {
        opacity: 1; 
    }
  }

::-webkit-scrollbar {
    width: 13px;
  }
  
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  ::-webkit-scrollbar-thumb {
    background: #666; 
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

table {
    overflow: scroll !important;
}
// #tddd {
//     height: 7vh;
// }
.col-md-9 {
    height: 100vh;
    overflow: scroll !important;
}
// .google-btn {
//     background-color: #2C91FF !important;
// }
.date-heading {
    color: #89C283;
}

.footer {
    a {
        color: #181818;
        &:hover {
            color: #000;
            border-bottom: 2px solid #000;
        }
    }
}
.radio {
    display: none;
    padding: 6px;
}

.radio-labels {
    margin: 0.4rem;
    padding: 6px;
    opacity: 0.7;
    &:hover {
        cursor: pointer;
        opacity: 1;
    }
}
input[type="radio"]:checked + label {
    color: #7F00AC;
    // font-weight: 600;
    // border-bottom: 3px solid #000;
    background-color: rgba(148, 5, 198, 0.15) !important;
    border-radius: 6px;
    transition: all 0.4s ease;
    transform: translateY(5px);
    font-size: 16px;
    ::after {
        filter: blur(10px);
        transition: 0.2s ease-in-out;
    }
}

.dropdown-toggle::after { 
    content: none !important; 
} 
.month-name {
    border-radius: 8px;
    color: #000;
    &:hover {
        background-color: rgba(255, 247, 202, 0.8);
        cursor: pointer;
    }
}
.btn {
    background: transparent !important;
    border: 0 !important;
    color: #000 !important;
}

.month-button {
    color: #000 !important;
}
input[type=number] {
  -moz-appearance: textfield;
  background: #fff !important;
  color: #000 !important;
  border: 2px #000 solid !important;
  border-radius: 6px !important;
}
.day {
    &:hover {
        background-color: rgba(56,190,248,0.2);
        // border-radius: 30% !important;
        color: #0C75A4;
        font-weight: 400;
        cursor: pointer;
    }
}
// .week-rows {
