//     border-radius: 4px;
// }

.day-year {
    &:hover {
        background-color: rgba(255, 247, 202, 1);
        border-radius: 50%;
        color: #000;
        font-weight: 700;
        cursor: pointer; 
    }
}

.alarm-btn {
    outline: none !important;
}
.some-wrapper {
    .react-timekeeper__clock-wrapper {
        background: #383838;
    }
    .react-timekeeper__clock {
        background: rgba(20, 20, 20, 0.2);
    }
} 

:root {
    --main-box-shadow: 0 3px 11px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.15);
    --main-bg: #000;
    --main-font-family: 'Work Sans';

    
    --top-bg:#383838;
    --top-colon-color: #B4B4B4;
    --top-text-color: #E1E1E1;
    --top-selected-color: rgba(13, 147, 223, 0.7);;
    --top-meridiem-color: #B4B4B4;

    --dropdown-border: 2px solid #000;
    --dropdown-shadow: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    --dropdown-text-color: #8c8c8c;
    --dropdown-hover-bg: #EAF8FF;

    --clock-wrapper-bg: #f4f4f4;
    --clock-bg: #fff;

    --hand-line-color: rgba(13, 147, 223, 0.4);
    --hand-circle-center: rgba(13, 147, 223, 0.7);
    --hand-circle-outer: rgba(13, 147, 223, 0.7);
    --hand-minute-circle: rgba(13, 147, 223, 0.7);
    
    --numbers-text-color: #fff;
    --numbers-font-size-reg: 16px; // 12h mode only
    --numbers-font-size-inner: 15px; // 24h mode only
    --numbers-font-size-outer: 13px;

    --meridiem-bg-color: #444;
    --meridiem-text-color: #999;
    --meridiem-selected-bg-color: rgba(13, 147, 223, 0.7);
    --meridiem-selected-text-color: #fff;
}
::selection {
    background-color: rgba(0, 231, 217, 0.2);
}
.week-day {
    padding: 0;
}
.fa-list-ul {
    &:hover {
        background: $backgroundColor;
        border-radius: 50%;
        cursor: pointer;
    }
}

.is-poppins {
    font-family: 'Poppins';
}

tr {
    border-top: hidden;
}

.signup {
    background-color: #222;
}

.signup-form {
    background-color: #181818;
    border-radius: 20px;
    color: #c1c1c1;
}
.logout-section {
    &:hover {
        transform: scale(1.1);
    }
}

ul {
    list-style-type: none;
}
.events {
    border-radius: 8px;
    tr {
        .eventName {
            &:hover {
                border-bottom: 2px solid #B10DFF;
                cursor: pointer;
                color: #B10DFF;
            } 
        }
    }
}
table {
    overflow: scroll;
    td {
        border-top: none !important;
    }
    thead {
        border-top: none !important;
    }
    th {
        border-top: none !important;
        vertical-align: top !important;
    }
}
.events-table {
    border-radius: 20px !important;
}
.fa-trash, .fa-pencil {
    // display: none !important;
    &:hover {
        color: #B10DFF;
    }
}
tr {
    &:hover {
        .fa-trash, .fa-pencil {
            display: block !important;
        }
    }
}
.right {

    color: #000 !important;
}
td {
    color: #000;
}

.navbar-right {
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

.add-event {
    background-color: rgba(255, 179, 0, 0) !important;
    // border-radius: 6px !important; 
    border-radius: 4px !important;
    border-bottom: 3px solid #000 !important;
    color: #000 !important;
    &:focus {
        outline: none !important;
        box-shadow: none !important;
        border-bottom: 3px solid #0C75A4 !important;
    }
}
.add-event-form {
    border-radius: 8px !important;
}
.btn-type-2 {
    padding: 4px;
    border: none  !important;
    background: #f7f1f1;
    border: 1px solid #383838 !important;
    border-radius: 4px;
    -webkit-transition: all .2s ease !important;
    -moz-transition: all .2s ease !important;
    transition: all .2s ease !important;
    &:hover {
        background: #f3f0f0;
    }
}
.uname {
    background-color: rgba(99, 189, 255, 0.2);
    color: #58B7FF;
    border-radius: 6px;
    transition: all 0.2s;
}
.btn {
    &:focus {
        outline: none !important;
        box-shadow: none !important;
    }
}
.add-event-btn {
    background: transparent !important;
    margin-top: 5px;
    font-family: 'Nunito';
    padding: 3px !important;
    border: 2px solid #000;
    border-radius: 4px ;
    transition-delay: 0.1s !important;
    -webkit-transition-property: background, transform;
    -webkit-transition-duration: 0.5s, 0.2s;
    &:focus {
        outline: none !important;
    }
    &:hover {
        .fa-plus {
            color: #fff !important;
            opacity: 0.9;
        }
        background: #000 !important;
        color: #fff;
        transition:all 0.2s !important;
        transform: translateY(-3px) !important;
        
    }
}
.left {
    background-color: #fff;
}
.container-email-verify {
    width: 90%;
    margin: 0 auto !important;
    height: 50vh;
}
.i-page {
    opacity: 0.5;
    font-size: 40px;
}

.search-input-box {
    color: #000;
    background: #fff;
    padding: 12px;
    border: 2px solid #000 !important;
    border-radius: 6px;
    border: 0;
}
::placeholder {
    color: #000;
    opacity: 0.6;
    font-family: 'Poppins';
  }
  
  :-ms-input-placeholder { 
   color: #000;
  }
  
  ::-ms-input-placeholder {
   color: #000;
  }

.email-display{
    display:none !important;
}
.remind {
    &:hover {
        background: rgba(255, 247, 202, 0.8);
        border-radius: 4px;
        cursor: pointer;
    }
}
.form-search button {
    background: transparent !important;
    border: none;
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    position: absolute;
    top: 0;
    right: 0;
    padding: 18px 25px;
    z-index: 2;
    color: #fff;
    opacity: 0.6;

    
  }
.form-search {
    input {
        -webkit-transition: all .2s ease !important;
    -moz-transition: all .2s ease !important;
    transition: all .2s ease !important;
        &:focus {
            width: 300px !important;
        }
    }
}
.arrow {
      &:hover {
          background: rgba(18,112,55,0.2);
          border-radius: 20%;
      }
}
.container-signup {
    padding-top: 4rem;
    width: 70%;
    margin: 0 auto;
}
.row-form {
    padding: 6rem;
    background-color:rgba(242, 232, 195, 0.514);
    border-radius: 10px;
}
.search-list {
    z-index: 2;
    position: absolute;
    
}
@media screen and (max-width:550) {
    .row {
        display: flex !important;
        flex-direction: column-reverse !important;
    }
}
@media screen and (min-width:991.98px) {
    .right {
        border-right: 1px solid rgb(217, 210, 210);
    }
}
